.header-pc {
  background-color: #E2E1CF;
  position: relative;
  transition: 0.2s ease-out;
}
.header-pc::before {
  content: "dfssfd";
  position: absolute;
  width: 50%;
  height: 100%;
  right: 0;
  clip-path: polygon(50% 0, 100% 0, 100% 100%, 0% 100%);
  z-index: 0;
}
.header-pc .navbar-fijo-Pc {
  position: absolute;
  position: fixed;
  transition: 0.5s ease-in-out;
  z-index: 10000;
  background-color: #D50474;
  width: 100%;
  box-sizing: border-box;
  top: -180px;
  padding: 16px;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.3);
}
.header-pc .navbar-fijo-Pc .logo {
  position: absolute;
  background-color: #FEED01;
  width: 100px;
  top: 0;
  border-radius: 0 0 16px 16px;
  padding: 16px;
  transition-delay: 0.2s;
}
.header-pc .navbar-fijo-Pc ul li {
  margin: 0 8px;
  transition: 0.2s ease-in;
  cursor: pointer;
  color: #FFFFFF;
  padding: 8px;
  border-radius: 16px 0 16px 0;
}
.header-pc .navbar-fijo-Pc ul li:hover {
  background-color: #6D4D45;
  color: #FEED01;
}
.header-pc .navbar-fijo-Pc .menu-secundario ul li {
  color: #FFFFFF;
  margin: 0 8px;
  transition: 0.2s ease-in;
  cursor: pointer;
  padding: 8px;
  border-radius: 16px 0 16px 0;
}
.header-pc .navbar-fijo-Pc .menu-secundario ul li:hover {
  background-color: #FEED01;
  color: #6D4D45;
}
.header-pc .navbar-fijo-Pc .menu-secundario img {
  width: 50px;
}
