.header-pc .slider {
  position: relative;
  z-index: 2;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 96px;
  row-gap: 96px;
}
.header-pc .slider .copy-slider {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.header-pc .slider .Carrousel-slider {
  background-color: #D50474;
  border-radius: 16px 0 0 0;
  overflow: hidden;
  position: relative;
  box-shadow: -1px -1px 36px -16px rgba(0, 0, 0, 0.73);
  -webkit-box-shadow: -1px -1px 36px -16px rgba(0, 0, 0, 0.73);
  -moz-box-shadow: -1px -1px 36px -16px rgba(0, 0, 0, 0.73);
}
.header-pc .slider .Carrousel-slider .control-imagenes {
  position: absolute;
  bottom: 16px;
  left: 16px;
  background-color: #FEED01;
  border-radius: 20px;
  padding: 4.8px;
}
.header-pc .slider .Carrousel-slider .control-imagenes .control {
  width: 15px;
  height: 15px;
  background-color: #D50474;
  border-radius: 50%;
  margin: 1.6px 4.8px;
  transition: 0.1s ease-in;
  cursor: pointer;
}
.header-pc .slider .Carrousel-slider .control-imagenes .control:hover {
  margin: 1.6px 9.6px;
}
.header-pc .slider .Carrousel-slider .imagenes-slider {
  width: 100%;
