.productos-destacados .card-producto-destacado .icono-destacado img {
  width: 20px;
}
.productos-destacados .card-producto-destacado .img-hover {
  position: absolute;
  height: 100%;
  border-radius: 16px;
  overflow: hidden;
  opacity: 0;
  transition: 0.2s ease-in-out;
}
.productos-destacados .card-producto-destacado .img-hover img {
  min-height: 100%;
  transition: 0.3s ease-in-out;
  transition-delay: 0.1s;
}
.productos-destacados .card-producto-destacado .card-imagen {
  border-radius: 16px 16px 0 0;
