.eventos {
  margin: 32px auto;
  background-color: #FEED01;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 0;
  row-gap: 0;
  transition: 0.2s ease-in-out;
}
.eventos .chef {
  position: relative;
  max-height: 200px;
}
.eventos .chef::after {
  z-index: 10;
  content: "";
  position: absolute;
  border-radius: 50%;
  background-color: #6D4D45;
  height: 300px;
  width: 300px;
  bottom: -150px;
  left: -50px;
  transition: 0.2s ease-in-out;
}
.eventos .chef img {
  position: relative;
  z-index: 20;
  width: 100%;
  transform: scale(0.8) translateY(-60px);
  transition: 0.3s ease-in-out;
  animation-delay: 0.1s;
}
.eventos .copy-eventos {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  padding: 16px 32px;
}
.eventos .imagen-evento {
  background-color: darkgreen;
}
.eventos .imagen-evento img {
  transition: 0.2s ease-out;
  height: 100%;
}
.eventos:hover {
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.171);
}
.eventos:hover .chef::after {
  height: 400px;
  width: 400px;
}
.eventos:hover .chef img {
  transform: scale(1.05);
  transform: translateX(30px);
}

.nuestras-sedes {
  background-color: #6D4D45;
