}
.nuestras-sedes .nuestras-sedes-items {
  position: relative;
  z-index: 250;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 32px;
  row-gap: 32px;
}
.nuestras-sedes .nuestras-sedes-items .titulo-sedes {
  grid-column: 1/3;
}
.nuestras-sedes .nuestras-sedes-items .card-sedes {
  border-radius: 16px;
  padding: 16px;
  background-color: #6D4D45;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 16px;
  row-gap: 16px;
}
.nuestras-sedes .nuestras-sedes-items .card-sedes .imagen {
  filter: drop-shadow(0px 0px 30px rgba(0, 0, 0, 0.4));
